.youtube {
	.inner {
		article {
			width: 400px; padding: 20px;
			> .inner {
				width: 100%; padding: 20px; border: 1px solid #bbb;
				.pic {
					width: 100%;
					img {
						width: 100%;
					}
				}
			}
			h2 {
				font: bold 16px/1.2 'arial'; color: #555; margin-bottom: 15px; margin-top: 20px;
			}
			p {
				font: 14px/1.2 'arial'; color: #777; margin-bottom: 25px; word-break: break-all;
			}
			span {
				font: 12px 'arial'; color: orange;
			}
		}
	}
}
.popup {
	iframe {
		width: 100%; height: 100%; object-fit: cover;
	}
}